.weather-card{
    background-color:rgba(254, 253, 253, 0.2);
    border-radius: 40px;
    width: fit-content;
    display:flex;
    flex-direction:column;
    margin:20px 10px;
    padding: 20px;
    color:white;
    font-weight:300;
}
.date{
    font-weight: 400;
    color:black;
    text-shadow: 1px 1px grey;

}
.maxTemp{
    font-size:60px;
    margin:0;
}
.navPoint{
    width:15%;
    height:50%;
    margin-right:10px;
}
.condition{
    display:flex;
    align-items:center;
    width: fit-content;
    color:white;
    font-weight:300;
    font-size:20px;
    text-shadow: 2px 2px black;
}
